* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
}

.header {
  background-color: #15803d;
  color: white;
  padding: 1rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.header-content {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-logo {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  cursor: pointer;
}

.header-logo:hover {
  opacity: 0.8;
}

.header-title {
  font-size: 1.5rem;
  font-weight: bold;
}

.header-nav {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.nav-button {
  background: none;
  border: none;
  color: white;
  font-size: 1rem;
  cursor: pointer;
  font-weight: 500;
}

.nav-button:hover {
  opacity: 0.8;
}

.cart-icon-wrapper {
  position: relative;
  cursor: pointer;
}

.cart-badge {
  position: absolute;
  top: -8px;
  right: -8px;
  background-color: #ef4444;
  color: white;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  font-weight: bold;
}

.landing-hero {
  position: relative;
  min-height: 100vh;
  background-image: url('https://images.unsplash.com/photo-1466781783364-36c955e42a7f?w=1600');
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.landing-overlay {
  position: absolute;
  inset: 0;
  background-color: rgba(0,0,0,0.5);
}

.landing-content {
  position: relative;
  z-index: 10;
  text-align: center;
  color: white;
  padding: 2rem;
  max-width: 800px;
}

.landing-logo {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 2rem;
}

.landing-title {
  font-size: 4rem;
  font-weight: bold;
}

.landing-description {
  font-size: 1.25rem;
  margin-bottom: 2rem;
  line-height: 1.8;
}

.btn-primary {
  background-color: #16a34a;
  color: white;
  font-weight: bold;
  padding: 1rem 2rem;
  font-size: 1.25rem;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
}

.btn-primary:hover {
  background-color: #15803d;
}

.page-container {
  min-height: 100vh;
  background-color: #f9fafb;
}

.content-wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.page-title {
  font-size: 2.5rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 2rem;
}

.category-section {
  margin-bottom: 3rem;
}

.category-title {
  font-size: 1.75rem;
  font-weight: 600;
  color: #15803d;
  margin-bottom: 1.5rem;
  border-bottom: 3px solid #86efac;
  padding-bottom: 0.5rem;
}

.products-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1.5rem;
}

.product-card {
  background-color: white;
  border-radius: 0.75rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  overflow: hidden;
  transition: transform 0.2s, box-shadow 0.2s;
}

.product-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 4px 16px rgba(0,0,0,0.15);
}

.product-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.product-info {
  padding: 1.25rem;
}

.product-name {
  font-size: 1.25rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.product-price {
  font-size: 1.75rem;
  font-weight: bold;
  color: #16a34a;
  margin-bottom: 1rem;
}

.btn-add-cart {
  width: 100%;
  padding: 0.75rem;
  font-size: 1rem;
  font-weight: 600;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
}

.btn-add-cart-active {
  background-color: #16a34a;
  color: white;
}

.btn-add-cart-active:hover {
  background-color: #15803d;
}

.btn-add-cart-disabled {
  background-color: #d1d5db;
  color: #6b7280;
  cursor: not-allowed;
}

.empty-cart {
  text-align: center;
  padding: 4rem;
}

.empty-cart-icon {
  margin: 0 auto 1rem;
  color: #d1d5db;
}

.empty-cart-text {
  font-size: 1.5rem;
  color: #6b7280;
  margin-bottom: 1.5rem;
}

.cart-layout {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 2rem;
}

@media (max-width: 768px) {
  .cart-layout {
    grid-template-columns: 1fr;
  }
}

.cart-items-container {
  background-color: white;
  border-radius: 0.75rem;
  padding: 1.5rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.cart-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem 0;
  border-bottom: 1px solid #e5e7eb;
}

.cart-item:last-child {
  border-bottom: none;
}

.cart-item-image {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 0.5rem;
}

.cart-item-info {
  flex: 1;
}

.cart-item-name {
  font-size: 1.125rem;
  font-weight: bold;
  margin-bottom: 0.25rem;
}

.cart-item-price {
  color: #6b7280;
}

.quantity-controls {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.quantity-btn {
  padding: 0.5rem;
  border: none;
  border-radius: 0.375rem;
  cursor: pointer;
}

.quantity-btn-minus {
  background-color: #e5e7eb;
}

.quantity-btn-plus {
  background-color: #16a34a;
  color: white;
}

.quantity-display {
  width: 3rem;
  text-align: center;
  font-weight: bold;
}

.cart-item-total {
  width: 5rem;
  text-align: right;
}

.cart-item-total-price {
  font-size: 1.125rem;
  font-weight: bold;
}

.btn-remove {
  padding: 0.5rem;
  color: #ef4444;
  background: none;
  border: none;
  cursor: pointer;
}

.btn-remove:hover {
  opacity: 0.7;
}

.order-summary {
  background-color: white;
  border-radius: 0.75rem;
  padding: 1.5rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.summary-title {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

.summary-details {
  margin-bottom: 1.5rem;
}

.summary-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.75rem;
  font-size: 1.125rem;
}

.summary-total {
  display: flex;
  justify-content: space-between;
  font-size: 1.5rem;
  font-weight: bold;
  border-top: 2px solid #e5e7eb;
  padding-top: 0.75rem;
}

.summary-total-amount {
  color: #16a34a;
}

.btn-checkout {
  width: 100%;
  background-color: #16a34a;
  color: white;
  font-weight: bold;
  padding: 0.75rem;
  font-size: 1rem;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  margin-bottom: 0.75rem;
}

.btn-checkout:hover {
  background-color: #15803d;
}

.btn-continue {
  width: 100%;
  background-color: #e5e7eb;
  color: #1f2937;
  font-weight: bold;
  padding: 0.75rem;
  font-size: 1rem;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.btn-continue:hover {
  background-color: #d1d5db;
}